<template>
  <v-app>
    <v-toolbar height="70">
      <v-toolbar-title>AlphaCat</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu open-on-hover bottom offset-y>
        <v-btn slot="activator" color="transparent" depressed class="head_buttons">
          {{workerName}}
          <v-icon dark>arrow_drop_down</v-icon>
        </v-btn>
        <v-list>
          <v-list-tile to="/worker/center">
            <v-list-tile-title></v-list-tile-title>
          </v-list-tile>
          <v-list-tile>
            <v-list-tile-title></v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-menu>
      <v-menu open-on-hover bottom offset-y>
        <v-btn slot="activator" color="transparent" depressed class="head_buttons">
          <v-badge color="red" v-model="show">
            <span slot="badge">{{number}}</span>
            <v-icon dark>mail</v-icon>
            
          </v-badge>
        </v-btn>
        <v-card>
          <v-card-text v-if="number === 0">
            ~
          </v-card-text>
          <v-card-text v-if="number !== 0">
            
            <v-list>
              <v-list-tile v-for="notice in notices" v-if="notice.state === 0" :key="notice.id" class="notice-list-item">
                <v-divider></v-divider>
                <v-list-tile-title  @click="showNotice(notice.id)">
                  <span class="body-2">{{notice.title}}</span>
                </v-list-tile-title>
              </v-list-tile>
            </v-list>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="blue" to="/worker/center">
              
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-menu>
    </v-toolbar>
    <v-toolbar color="primary" height="50">
      <v-toolbar-items>
        <v-btn dark flat to="/worker/index"></v-btn>
      </v-toolbar-items>
      <v-toolbar-items>
        <v-btn dark flat to="/worker/task"></v-btn>
      </v-toolbar-items>
      <v-toolbar-items>
        <v-btn dark flat to="/worker/rank"></v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-content>
      <router-view></router-view>
    </v-content>
    <v-dialog v-model="dialog" max-width="600px">
      <v-card>
        <v-card-title>
          <span>{{notice.title}}</span>
        </v-card-title>
        <v-card-text>
          {{notice.content}}
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" flat @click.stop="readNotice"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
  export default {
    name: "WorkerPage",
    computed: {
      show() {
        return this.number !== 0;
      }
    },
    data() {
      return {
        dialog: false,
        workerName: sessionStorage.getItem("name"),
        notices: [],
        number: 0,
        notice: {},
      }
    },
    methods: {
      update() {
        this.$http.get('/notice')
          .then((response) => {
            this.notices = response.data;
            this.number = 0;
            for (let i = 0; i < this.notices.length; i++) {
              if (this.notices[i].state === 0) this.number++;
            }
          })
      },
      showNotice(id) {
        this.$http.get('/notice/'+id)
          .then((response) => {
            this.notice = response.data;
            this.dialog = true;
          })
      },
      readNotice() {
        this.$http.post('/notice/'+this.notice.id+'/read')
          .then((response) => {
            this.update();
          });
        this.dialog = false;
      }
    },
    created() {
      this.update();
    }
  }
</script>

<style scoped>
.notice-list-item{
  height: 30px;
}
</style>



