<template>
  <v-card height="100%">
    <v-card-title>
      <span class="headline"></span>
    </v-card-title>
    <v-card-text>
      <v-form :model="admin">
        <v-container>
          <v-layout row>
            <v-flex xs6>
              <v-text-field v-model="admin.name" label="" prepend-icon="account_box" required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs6>
              <v-text-field v-model="admin.actualName" label="" prepend-icon="perm_identity" required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs6>
              <v-text-field v-model="admin.key" label="" prepend-icon="lock" type="password" required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs5>
              <v-radio-group v-model.number="admin.sex" row prepend-icon="supervisor_account">
                <v-radio label="" v-bind:value=0></v-radio>
                <v-radio label="" v-bind:value=1></v-radio>
              </v-radio-group>
            </v-flex>
          </v-layout>
        </v-container>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-layout row>
        <v-spacer></v-spacer>
        <v-flex xs8>
          <v-btn flat color="deep-orange darken-4" @click="save"></v-btn>
          <v-btn flat color="amber" @click="getInfo"></v-btn>
        </v-flex>
      </v-layout>
    </v-card-actions>
  </v-card>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "AdminInfo",
    data() {
      return {
        adminId: sessionStorage.getItem("id"),
        admin: {
          name: '',
          actualName: '',
          sex: 0,
          key: ''
        }
      }
    },
    methods: {
      getInfo() {
        this.$http.get('/admin/'+this.adminId)
          .then((response) => {
            this.admin = response.data;
            delete this.admin.auth;
          });
      },
      save() {
        this.$http.put('/admin/'+this.adminId, this.admin)
          .then((response) => {
            bus.$emit('show-info', '');
          })
          .catch((error) => {
            bus.$emit('show-info', '');
          })
      }
    },
    created() {
      this.getInfo();
    }
  }
</script>

<style scoped>

</style>



