<template>
  <v-layout row>
    <v-flex xs10 offset-xs1>
      <v-card>
        <v-card-title>
          <span class="headline"></span>
          <v-spacer></v-spacer>
          <v-btn @click="openDialog(null)">
            <v-icon>add</v-icon>
            <span></span>
          </v-btn>
        </v-card-title>
        <v-data-table :headers="headers" :items="desserts" v-model="selected" item-key="id" hide-actions>
          <template slot="items" slot-scope="props">
            <tr>
              <td>{{ props.item.id }}</td>
              <td>{{ props.item.name }}</td>
              <td>{{ props.item.actualName }}</td>
              <td>{{ getSex(props.item.sex)}}</td>
              <td>{{ getAdminType(props.item.auth)}}</td>
              <td class="layout px-0">
                <v-layout row justify-space-around>
                  <v-flex xs-1>
                    <v-btn icon class="mx-0" @click="openDialog(props.item)">
                      <v-icon color="teal">edit</v-icon>
                    </v-btn>
                  </v-flex>
                  <v-flex xs-1>
                    <v-btn icon class="mx-0" @click="openDelDialog(props.item)">
                      <v-icon color="blue-grey">delete</v-icon>
                    </v-btn>
                  </v-flex>
                </v-layout>
              </td>
            </tr>
          </template>
          <template slot="no-data">
            <v-alert :value="true" color="error" icon="warning">
               :(
            </v-alert>
          </template>
        </v-data-table>
      </v-card>
    </v-flex>
    <v-dialog v-model="editDialog" max-width="500" lazy hide-overlay persistent>
      <v-card>
        <v-card-title class="headline">{{dialogTitle}}</v-card-title>
        <v-card-text>
          <v-form :model="admin">
            <v-container fluid>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field v-model="admin.name" label="" prepend-icon="account_box" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field v-model="admin.actualName" label="" prepend-icon="perm_identity" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs10>
                  <v-text-field v-model="admin.key" label="" prepend-icon="lock" type="password" required></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs10>
                  <v-select :items="authItems" v-model.number="admin.auth" item-value="value" label="" prepend-icon="vpn_key"></v-select>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs5>
                  <v-radio-group v-model.number="admin.sex" row prepend-icon="supervisor_account">
                    <v-radio label="" v-bind:value=0></v-radio>
                    <v-radio label="" v-bind:value=1></v-radio>
                  </v-radio-group>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click="submitItem()"></v-btn>
          <v-btn color="green darken-1" flat @click.native="editDialog = false"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="deleteDialog" max-width="500px" hide-overlay persistent>
      <v-card>
        <v-card-title>
          <span>{{admin.name}} </span>
          <v-spacer></v-spacer>
        </v-card-title>
        <v-card-actions>
          <v-btn color="red" flat @click="deleteItem"></v-btn>
          <v-btn color="primary" flat @click.stop="deleteDialog=false"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "AdminTable",
    computed: {
    },
    data() {
      return {
        drawer: false,
        editDialog: false,
        dialogTitle: '',
        deleteDialog: false,
        color: 'indigo',
        selected: [],
        authItems: [
          {text: '', value: 1},
          {text: '', value: 2}
        ],
        listItem: [
          {icon: 'people', text: '', path: '/superAdmin/index'},
          {icon: 'poll', text: '', path: ''},
          {icon: 'timeline', text: '', path: ''},
          //{icon: '', text: '', path: ''},
        ],
        headers: [
          {text: '', sortable: true, value: 'id'},
          {text: '', sortable: false, value: 'name'},
          {text: '', sortable: false, value: 'actualName'},
          {text: '', sortable: false, value: 'sex'},
          {text: '', sortable: false, value: 'auth'},
          {text: '', sortable: false, value: 'action'}
          //{text: '', sortable: '', value: ''},
        ],
        desserts: [],
        adminIndex: 0,
        adminId: 0,
        admin: {
          name: '',
          actualName: '',
          sex: 0,
          auth: 1,
          key: ''
        }
      }
    },
    methods: {
      getSex: function(sex) {
        return sex === 0 ? '' : '';
      },
      getAdminType: function(auth) {
        return auth === 1 ? '' : '';
      },
      openDialog(item) {
        if (item == null) {
          this.admin = {name: '', actualName: '', sex: 0, auth: 1, key: ''};
          this.dialogTitle = '';
          this.adminId = -1;
        } else {
          this.adminId = item.id;
          this.adminIndex = this.desserts.indexOf(item);
          this.$http.get('/admin/'+this.adminId)
            .then((response) => {
              this.admin = response.data;
            });
          this.dialogTitle = '';
        }
        this.editDialog = true;
      },
      openDelDialog(item) {
        this.deleteDialog = true;
        this.adminId = item.id;
        this.admin.name = item.name;
      },
      submitItem() {
        this.editDialog = false;
        if (this.adminId < 0) {
          this.$http.post('/admin', this.admin)
            .then((response) => {
              bus.$emit('show-info', "" + this.admin.name);
            });
          this.$http.get('/admin')
            .then((response) => {
              this.desserts = response.data;
            })
        } else {
          this.$http.put('/admin/'+this.adminId+'/setAuth', {auth: this.admin.auth})
            .then((response) => {
            })
            .catch((error) => {
              bus.$emit('show-info', this.admin.name + "");
            });
          const auth = this.admin.auth;
          delete this.admin.auth;
          this.$http.put('/admin/'+this.adminId, this.admin)
            .then((response) => {
              this.admin.auth = auth;
              const newItem = this.admin;
              newItem.id = this.adminId;
              this.desserts.splice(this.adminIndex, 1, newItem);
              console.log(this);
              bus.$emit('show-info', this.admin.name + "");
            })
        }
      },
      deleteItem() {
        this.deleteDialog = false;
        this.$http.delete('/admin/'+this.adminId)
          .then((response) => {
            this.desserts.splice(this.adminIndex, 1);
            bus.$emit('show-info', this.admin.name + "");
          })
      }
    },
    created() {
      this.$http.get('/admin')
        .then((response) => {
          this.desserts = response.data;
        })
    },
  }
</script>

<style scoped>

</style>



